<template>
  <div>
    <img class="pic" ref="moxa" src="./assets/moxa.png"/>
    <img class="pic" ref="gbox" src="./assets/gbox.png"/>
    <img class="pic" ref="GTI_PC" src="./assets/PC.png"/>
    <img class="pic" ref="GW" src="./assets/tarelka.png"/>
    <img class="pic" ref="CAM" src="./assets/cam.png"/>
    <img class="pic" ref="WF" src="./assets/wi-fi.png"/>
    <img class="pic" ref="WFC" src="./assets/wi-fi-client.png"/>
    <img class="pic" ref="commut" src="./assets/commut.png"/>
    <img class="pic" ref="dell" src="./assets/dell140.png"/>

    {{isawait}}  {{isawaitscheme}}
    <br/>
    <select v-model="selectcbke">
      <option class="item-a" v-for="item in predrects" :key="item.id" :value="item">{{ item.name }} {{ item.serial }}
      </option>
    </select>


    <input v-model="TextIP" placeholder="IP">


    <select v-model="SelectedType">
      <option disabled value="">Выберите один из вариантов</option>
      <option>GBOX</option>
      <option>GTI_PC</option>
      <option>Wi-Fi</option>
      <option>Wi-Fi_Main</option>
      <option>Camera</option>
      <option>Video_reg</option>
      <option>Moxa</option>
      <option>Gateway</option>
      <option>GBOX_Reserve</option>
      <option>Switch</option>
      <option>Dell140</option>

    </select>

    <input type="checkbox" id="GTI" value=0 v-model="checkedServices">
    <label for="GTI">GTI</label>
    <input type="checkbox" id="ZTS" value=1 v-model="checkedServices">
    <label for="ZTS">ZTS</label>
    <input type="checkbox" id="KRS" value=2 v-model="checkedServices">
    <label for="KRS">KRS</label>
    <input type="checkbox" id="GRP" value=3 v-model="checkedServices">
    <label for="KRS">GRP</label>
    <input type="checkbox" id="VID" value=4 v-model="checkedServices">
    <label for="VID">VIDEO</label>
    <input type="checkbox" id="REP" value=5 v-model="checkedServices">
    <label for="REP">REPORTING</label>



    <button v-on:click="addKE">ADD</button>
    {{ TextIP }}
    <br/>
    <button v-on:click="save">save</button>
    <br/>
    <div class="item-inline" v-for="item in rectangle" :key="item.id">
      <div id="d1">
        {{ item.name }} {{ item.id }}
        <button v-on:click="deleteke(item.id)">Удалить</button>
      </div>
    </div>

    <div class="container">
      <div class="stages">
        <v-stage v-on:click="clickhandler" ref="stage" :config="stageSize">

          <v-layer ref="layer">

          </v-layer>

        </v-stage>
      </div>
      <div class="tower">
      {{ clickedobject }}

      Связать с: {{ prevclicked }}
        <br/>
        <button v-on:click="addcons">Add connector</button>
      <br/>
      <div v-for="item in cons" :key="item">

        {{ item }}
        <button v-on:click="deleteconn(item)">Удалить</button>
      </div>
      </div>

    </div>


  </div>
</template>

<script>
import axios from "axios";
import FileSaver from 'file-saver';
const width = window.innerWidth * 0.7;
const height = window.innerHeight - 150;
import Konva from 'konva';


export default {
  name: 'HelloWorld',

  data() {
    return {
      isawaitscheme: "Ожидаем загрузку схемы.",
      isawait: "Ожидаем информации по скважине.",
      clickedobject: "",
      prevclicked: "",

      selectcbke: null,
      wellkeinfo: null,
      chosed: null,
      SelectWELL: null,
      toshow: null,
      SelectPR: null,
      projs: null,
      wells: null,
      cons: [],
      selectKE: null,
      checkedServices: [],
      connecters: null,
      counterobjects: 0,
      SelectedType: null,
      TextIP: null,
      wellname: null,
      wellinfo: null,
      sourceGbox: new Image(100, 50),
      sourceGTI: new Image(100, 50),
      sourceGateway: new Image(100, 50),
      sourceCam: new Image(100, 50),
      sourceWiFi: new Image(100, 50),
      sourceWiFiClient: new Image(100, 50),
      sourceMoxa: new Image(100, 50),
      sourceSwitch: new Image(100, 50),
      sourceDell: new Image(100, 50),
      list: null,
      confnewline: null,
      stage: null,
      rectX: [],
      rectY: [],
      kenow: "-1",
      tester: null,
      prevdata: null,
      prevtrigg: 0,

      stageSize: {
        width: width,
        height: height,
        id: "foo"
      },
      configLine: {                   //line
        points: [],
        stroke: 'black',
        strokeWidth: 2,
      },
      isDragging: false,
      rectangle: [],
      predrects: [{"name": "Newobject", "id": "-1"}],
    }
  },
  watch: {
    prevdata: function(){
      this.isawaitscheme = "Схема загружена."
    },
    wellkeinfo: function(){
      this.isawait="Информация по КЕ загружена."
    },

    clickedobject: function () {
      this.load();
    },

    SelectPR: function () {
      this.setToshow();
    },
    SelectWELL: function () {
      this.chosed = Date.now().valueOf();
    },
    selectcbke: function () {
      if (this.selectcbke.name != "Newobject") {
        this.replacr(this.selectcbke.id);
        this.tester = 1;
      } else {

        this.kenow = String(parseInt(Date.now().valueOf())%1000000)+ String(this.counterobjects);
        this.tester = 0;
      }
    },
  },
  beforeMount() {
    this.getKEs();
  },
  mounted() {
    console.log(Date.now().valueOf())
    //this.setImages();

    //this.configLine.points.push(this.rectX[0], this.rectY[0], this.rectX[1], this.rectY[1]);
    console.log(this.$refs.layer.getNode())
    //this.load();
    //this.$nats.publish('gratata',1)

  },
  methods: {
    clickhandler(e) {
      console.log(e);
      let clicked = "nachalo";
      console.log(this.clickedobject);
      console.log(clicked);
      if (e.target.parent != null) {
        clicked = e.target.parent.attrs.id;
        console.log('clicked on', e.target.parent.attrs.id, clicked);
        this.helpfun(clicked);

      } else {
        this.prevclicked = "";
        this.clickedobject = "";

      }
    },
    helpfun(x) {
      if (this.prevclicked == "") {
        this.prevclicked = this.clickedobject;
      } else {
        this.prevclicked = "";
      }
      this.clickedobject = x;
      console.log(this.clickedobject);
      console.log("prev" + this.prevclicked)
    },
    deleteke(id) {
      let x = this.rectangle;
      for (let [i, obj] of x.entries()) {
        if (obj.id == id) {
          x.splice(i, 1)
        }
      }
      this.rectangle = x;

      let y = this.cons;
      for (let [i, obj] of y.entries()) {
        if (obj[0] == id || obj[1] == id) {
          y.splice(i, 1)
        }
      }
      this.cons = y;

      this.load();
    },
    deleteconn(id) {
      let y = this.cons;
      for (let [i, obj] of y.entries()) {
        if (obj == id) {
          y.splice(i, 1)
        }
      }
      this.cons = y;

      this.load();
    },

    replacr(x) {

      this.kenow = x;

      console.log(this.kenow);
    },
    prevfun() {
      console.log("PREVDATA",this.prevdata)

      this.prevtrigg = 0;
      if (this.prevdata.Wellinfo.length != 0) {
        this.prevtrigg = 1;

      }
      console.log("PREVDATA TO CHANGE", this.prevtrigg)
      this.cons = this.prevdata.Connecters;
      this.rectangle = this.prevdata.Wellinfo;
      this.load();
      this.counterobjects = this.SelectWELL.monitorstatus;
      console.log(this.counterobjects);




    },
    getKEs() {
      axios.get('http://localhost:8000/startup')
          .then(response => (this.wellkeinfo = response.data, this.setcbke()))
      axios.get('http://localhost:8000/startupscheme')
          .then(response => (this.prevdata = response.data, this.prevfun()))

    },
    setcbke() {
      var res = [{"name": "Newobject", "id": "-1"}];
      this.wellkeinfo.forEach(function (item) {
        res.push({
          "name": item.type,
          "id": String(item.CbId),
          "serial": item.serial,
        });

      })
      this.predrects = res;
    },
    setToshow: function () {
      let wellstoshow = [];
      let allw = this.wellinfo;
      let now = this.SelectPR;
      allw.forEach(function (item) {
        if (item.projectid == now) {

          wellstoshow.push({Name: item.Name, id: item.id, monitorstatus: item.monitorstatus});
        }
      })
      this.toshow = wellstoshow;
      console.log(wellstoshow);
    },
    setWells() {
      let projects = [];
      let wells = [];
      let jsonchik = this.wellinfo;
      jsonchik.forEach(function (item, i, jsonchik) {
        var index = projects.findIndex(obj => obj.Name == item.projectid);
        if (index == -1) {
          projects.push({Name: jsonchik[i].projectid});
          wells.push({prid: projects.findIndex(obj => obj.Name == item.projectid), Name: jsonchik[i].Name});
        } else {
          wells.push({prid: projects.findIndex(obj => obj.Name == item.projectid), Name: jsonchik[i].Name});
        }
      })
      this.projs = projects;
      this.wells = wells;
    },
    addcons() {
      var l = String(this.clickedobject);
      let p = String(this.prevclicked);
      console.log(l)
      if (this.clickedobject != null && this.prevclicked != null) {
        this.cons.push([l, p]);
      }
      console.log(this.selectKE);
      console.log(this.cons);
      this.clickedobject = "";
      this.prevclicked = "";
      this.load();
    },

    addKE() {
      let tea = "";
      console.log(this.kenow);
      if (this.tester == 0) {
        tea = String(parseInt(Date.now().valueOf())%1000000)+ String(this.counterobjects);
        console.log("NEWKE")
      } else {
        tea = this.kenow;
        console.log("OLDKE");
      }
      console.log(tea);
      this.rectangle.push({
        "x": 146,
        "y": 269,
        "width": 100,
        "height": 100,
        "fill": "green",
        "prtext": "",
        "name": this.SelectedType,
        "cornerRadius": 10,
        "ip": this.TextIP,
        "services": this.checkedServices,
        "shadowColor": "black",
        "shadowBlur": 5,
        "shadowOpacity": 0.8,
        "shadowOffset": {
          "x": 5,
          "y": 5
        },
        "id": tea,
      });

      this.kenow = tea;
      console.log()
      if (this.kenow == String(parseInt(Date.now().valueOf())%1000000)+ String(this.counterobjects))
      {
        this.counterobjects = this.counterobjects + 1;
      }

      console.log(tea, String(Date.now().valueOf())+"100" + this.counterobjects);
      this.load();
    },

    setImages() {
      console.log("SETTING");
      this.sourceGbox.src = this.$refs.gbox.src;
      this.sourceGTI.src = this.$refs.GTI_PC.src;
      this.sourceGateway.src = this.$refs.GW.src;
      this.sourceCam.src = this.$refs.CAM.src;
      this.sourceWiFi.src = this.$refs.WF.src;
      this.sourceWiFiClient.src = this.$refs.WFC.src;
      this.sourceMoxa.src = this.$refs.moxa.src;
      this.sourceSwitch.src = this.$refs.commut.src;
      this.sourceDell.src = this.$refs.dell.src;


      console.log("ENDSETTING", this.sourceGbox.src, this.sourceMoxa);

    }
    ,
    load() {
      this.setImages()
      //const data = localStorage.getItem('storage') || '[]';
      //console.log(this.wellinfo)
      //const data = [{"x":146,"y":269,"width":100,"height":100,"fill":"green","name":"GBOX","draggable":false,"cornerRadius":10,"shadowColor":"black","shadowBlur":5,"shadowOpacity":0.8,"shadowOffset":{"x":5,"y":5},"id":"450"},{"x":233,"y":95,"width":100,"height":100,"fill":"red","name":"Сборщик ГТИ","draggable":false,"cornerRadius":10,"shadowColor":"black","shadowBlur":5,"shadowOpacity":0.8,"shadowOffset":{"x":5,"y":5},"id":"1"},{"x":563,"y":343,"width":100,"height":100,"fill":"green","name":"Шлюз","draggable":false,"cornerRadius":10,"shadowColor":"black","shadowBlur":5,"shadowOpacity":0.8,"shadowOffset":{"x":5,"y":5},"id":"2"}]
      // this.rectangle = this.wellinfo.Wellinfo;
      // this.wellname = this.wellinfo.CBINFO[0].f3641;

      console.log(this.rectangle)
      this.Connectors();
    }
    ,



    save() {
      localStorage.setItem('storage', JSON.stringify(this.rectangle));
      console.log(JSON.stringify(Date.now().valueOf()));
      console.log(JSON.stringify(this.rectangle));
      console.log(JSON.stringify(this.cons));


      let k = '';
      let url = '';
      console.log("PREVTRIG", this.prevtrigg)
      console.log(this.prevdata)
      if (this.prevtrigg == 0) {
        k = 'post';
        url = 'http://localhost:8000/createwell';
        this.prevtrigg = 1;
      } else {
        k = 'put';
        url = 'http://localhost:8000/updatewell';
      }
      var blob = new Blob([JSON.stringify({status: 1,
            wellid: parseInt(this.wellkeinfo[0].wellid),
            connectors: JSON.stringify(this.rectangle),
            lines: JSON.stringify(this.cons)})],
          { type: "applications/json;charset=utf-8" });

      FileSaver.saveAs(blob, "static.txt");

      axios({
        method: k,
        url: url,
        data: {
          status: 1,
          wellid: parseInt(this.wellkeinfo[0].wellid),
          connectors: JSON.stringify(this.rectangle),
          lines: JSON.stringify(this.cons)
        }
      });

    }
    ,
    Connectors: function () {
      let prevclicked = this.prevclicked;
      let clickedobject = this.clickedobject;

      let layer = this.$refs.layer.getNode();
      layer.children.splice(0, layer.children.length);

      var arr = this.cons;

      function generateConnectors() {

        let result = [];
        console.log(arr);
        arr.forEach(function (item) {
          console.log(item);
          var from = item[0];
          var to = item[1];
          result.push({
            id: 'connector-' + result.length,
            from: from,
            to: to,
          });


        })

        //

        // from = rects[0].id;
        // to = rects[1].id;
        //
        // result.push({
        //   id: 'connector-' + result.length,
        //   from: from,
        //   to: to,
        // });


        return result;
      }

      function getConnectorPoints(from, to) {


        return [
          from.x + 50,
          from.y + 50,
          to.x + 50,
          to.y + 50,
        ];
      }

      var connectors = generateConnectors();
      var rectangle = this.rectangle;
      this.confnewline = connectors;

      function updateObjects() {

        rectangle.forEach((target) => {

          var node = layer.findOne('#' + target.id);

          node.x(target.x);
          node.y(target.y);
        });
        rectangle.forEach((target) => {

          var node = layer.findOne('#' + target.id);

          node.x(target.x);
          node.y(target.y);
        });
        connectors.forEach((connect) => {
          var line = layer.findOne('#' + connect.id);
          var fromNode = layer.findOne('#' + connect.from);
          var toNode = layer.findOne('#' + connect.to);
          const points = getConnectorPoints(
              fromNode.position(),
              toNode.position()
          );
          line.points(points);
        });
        layer.batchDraw();
      }

      connectors.forEach((connect) => {
        var line = new Konva.Line({
          stroke: 'black',
          id: connect.id,
          fill: 'black',
        });
        layer.add(line);
      });

      rectangle.forEach((target) => {
        var images = new Image(100, 50)
        if (target.name == "GBOX" || target.name == "GBOX_Reserve") {
          images = this.sourceGbox;
        } else if (target.name == "Gateway") {
          images = this.sourceGateway;

        } else if (target.name == "GTI_PC"|| target.name == "Video_reg") {
          images = this.sourceGTI;

        } else if (target.name == "Moxa") {
          images = this.sourceMoxa;

        } else if (target.name == "Camera") {
          images = this.sourceCam;

        } else if (target.name == "Wi-Fi") {
          images = this.sourceWiFiClient;

        } else if (target.name == "Wi-Fi_Main") {
          images = this.sourceWiFi;
        } else if (target.name == "RUH") {
          images = this.sourceMoxa;
        } else if (target.name == "Dell140") {
          images = this.sourceDell;
        } else if (target.name == "Switch") {
          images = this.sourceSwitch;
        }
        let fill = "green"
        if (target.id == prevclicked || target.id == clickedobject) {
          fill = "yellow"
        }
        var node = new Konva.Rect({
          y: 50,
          draggable: false,
          fill: fill,
          width: 100,
          height: 50,
          shadowBlur: 10,
        });
        var group = new Konva.Group({
          id: target.id,
          fill: target.fill,
          x: target.x,
          y: target.y,
          width: 100,
          height: 100,
          shadowBlur: 10,
          draggable: true,

        });
        var rectText = new Konva.Text({
          text: target.name + "\n" + target.ip,
          fontSize: 14,
          id: target.id,
          y: 60,
          draggable: false,
        });
        var itemImmage = new Konva.Image({
          image: images,
          width: 100,
          height: 50,
        });

        group.add(node);
        group.add(rectText);
        group.add(itemImmage)
        layer.add(group);


        group.on('dragmove', () => {
          // mutate the state
          target.x = group.x();
          target.y = group.y();

          // update nodes from the new state
          updateObjects();
        });
      });


      setTimeout(updateObjects, 70);
    },


    apiRes: function () {

      this.Connectors();
    },


  },

}
</script>
<style>
.pic {
  width: 0px;
  height: 0px;
  opacity: 0;
}

.item-inline {
  display: inline-flex;
  margin-left: 10px;
}

.tower {
  position: relative;
  bottom: 500px;
  left: 80%;
  grid-column-start: 2;
  grid-column-end: 2;
  grid-row-start: 1;
  grid-row-end: 1;
}

#d1 {
  display: grid;
}
.container {
  grid-template-columns: auto auto;
  grid-template-rows: auto;
}
.stages{
  width: 500px;
  height: 500px;
  grid-column-start: 1;
  grid-column-end: 1;
  grid-row-start: 1;
  grid-row-end: 1;
}

</style>